@import '../../../../sass/base/variables';

.option {
  cursor: pointer;
  position: relative;
  outline: none;
  border: none;
  font-family: inherit;
  font-size: inherit;
  background-color: inherit;
  text-align: center;

  &__img {
    width: 85%;
  }

  &__checkmark {
    position: absolute;
    right: 0;
    height: 3rem;
    width: 3rem;
    background-color: white;
    border-radius: 100%;
    border: 1px solid $border-color;
    color: $primary-red;
  }

  &__name {
    position: absolute;
    background-color: $color-grey-light-1;
    top: 50%;
    height: 50%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-top: 3px solid $color-grey-light-4;

    &--selected {
      border-top: 3px solid $primary-red;
    }
  }
}
