@import '../../sass/base/variables';
@import '../../sass/abstracts/mixins';

.header {
  position: fixed;
  top: 0;
  width: 100%;
  height: $header-height;
  background-color: $primary-black;
  z-index: 1000;
  display: flex;
  justify-content: space-between;

  &__main {
    display: flex;
  }

  &__logo-title {
    margin-left: 2rem;
    margin-right: 4rem;
    display: flex;
    align-items: center;
  }

  &__logo {
    height: 4rem;
    width: auto;
    margin-right: 1rem;

    @include respond(tab-port) {
      height: 3.5rem;
    }
  }

  &__title {
    display: flex;
    justify-content: center;
    font-family: 'Permanent Marker';
    color: white;
    font-size: 2.4rem;

    @include respond(tab-port) {
      font-size: 2rem;
    }
  }

  &__menu {
    display: none;

    @include respond(tab-port) {
      display: block;
    }
  }

  &__menu-items {
    @include respond(tab-port) {
      display: none;
    }
  }
  &__account {
    display: flex;
    flex: 0 0;
  }

  &__account-actions {
    display: flex;

    @include respond(phone) {
      display: none;
    }
  }

  &__cart {
    position: relative;
  }

  &__icon {
    font-size: 3rem;
    color: white;
    height: 3.5rem;
    display: flex;
    align-items: center;
  }

  &__alert-text {
    margin-bottom: 2rem;
  }

  &__checkout {
    width: 75%;
  }

  &__cart-items {
    position: absolute;
    right: -0.6rem;
    top: -0.25rem;
    height: 1.7rem;
    width: 1.7rem;
    border-radius: 100%;
    background-color: $primary-red;
    color: white;
    font-size: 1.2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    box-shadow: 0 0.1rem 0.1rem rgba(0, 0, 0, 0.5);
  }
}

.animate-item-added {
  animation: show-item-added 4s;
}

@keyframes show-item-added {
  0% {
    opacity: 1;
  }

  75% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

.animate-num-items {
  animation: pulsate 1s 3;
}

@keyframes pulsate {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.4);
  }

  100% {
    transform: scale(1);
  }
}
