@import '../../../sass/base/variables';
@import '../../../sass/abstracts/mixins';

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  overflow-y: scroll;
  z-index: 1200;
  padding: 2rem;

  @include respond(phone) {
    padding: 0.5rem;
  }
}
