@import '../../sass/base/variables';
@import '../../sass/abstracts/mixins';

.totalBuilder {
  display: flex;
  width: 100%;
  height: 100%;

  @include respond(tab-port) {
    flex-direction: column;
  }

  @include respond(phone) {
    flex-direction: column;
    font-size: 1.2rem;
  }

  &__mypizza {
    background-color: $color-grey-light-1;
    align-self: flex-start;
    position: relative;
    width: 40%;
    min-width: 25rem;
    margin-right: 5rem;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid $border-color;
    border-radius: 1rem;

    @include respond(tab-port) {
      width: 100%;
      margin-bottom: 2rem;
    }
  }

  &__details {
    width: 100%;

    @include respond(tab-port) {
      border: none;
    }
  }

  &__preview {
    width: 100%;

    @include respond(tab-port) {
      display: none;
    }
  }

  &__builder {
    background-color: $color-grey-light-1;
    align-self: flex-start;
    width: 60%;
    border: 1px solid $border-color;
    border-radius: 1rem;
    overflow: hidden;

    @include respond(tab-port) {
      width: 100%;
    }
  }
}
